angular.module('myApp').directive('headerDirective', ['usersEntity','socket','$location','$translate', function(usersEntity,socket, $location, $translate){
    return {
        restrict: 'E',
        replace: true,
        templateUrl: 'header',
        link: function(scope, element, attribute){

            socket.on('connexion', function(a){
                console.log(a);
            });
            scope.setLang = function(lang){
                $translate.use(lang);
            };
        }
    }
}]);
